.section-title {
  position: relative;
  font-size: 54px;
  line-height: 1.3;
  font-weight: 400;
  color: #1e1e1e;
  text-transform: uppercase;
  font-family: "Cocogoose", sans-serif;
}

.hero__container {
  max-width: none;
}
.hero__wrap {
  display: flex;
  align-items: center;
}
.hero__info {
  width: 40%;
}
.hero__title {
  font-size: 70px;
  line-height: 1.3;
  font-weight: 800;
  margin-bottom: 24px;
}
.hero__subtitle {
  font-size: 28px;
  line-height: 1.5;
  font-weight: 500;
  margin-bottom: 33px;
}
.hero__text {
  font-size: 24px;
  line-height: 1.4;
  margin-bottom: 40px;
}
@media screen and (max-width: 1700px) {
  .hero__container {
    max-width: 1160px;
    padding: 0 20px;
  }
}
.hero {
  padding: 32px 0 0px;
  position: relative;
  margin-right: 11px;
}
.hero__container {
  position: relative;
  max-width: none;
}
.hero__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: "Noto Sans", "sans-serif";
  margin: 0 auto;
}
.hero__list {
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero__item:not(:last-child) {
  margin-right: 38px;
}
.hero__title {
  text-transform: uppercase;
  font-weight: 800;
  font-size: 60px;
  color: rgb(23, 17, 105);
  line-height: 1.3;
  margin-bottom: 24px;
}
.hero__subtitle {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  color: rgb(23, 17, 105);
  margin-bottom: 36px;
}
.hero__content {
  max-width: 400px;
}
.hero__image {
  position: absolute;
  top: 40px;
  z-index: 10;
}
.hero__image-1 {
  left: 0;
}
.hero__image-2 {
  right: 0;
}
.hero #shmu-widget {
  min-height: 604px;
  width: 497px;
  background-color: #fff;
  border-radius: 30px;
  margin-bottom: 22px;
}
.hero .hero-move {
  position: absolute;
  transform: rotate(26.7deg);
}
.hero .heart-1 {
  top: 130px;
  right: 52px;
}
.hero .heart-2 {
  bottom: 189px;
  left: 28px;
}
.hero .heart-3 {
  bottom: 238px;
  right: 50px;
}
.hero .telegram {
  top: 175px;
  right: 57px;
}
.hero .messenger {
  top: 248px;
  left: 15px;
}
.hero .discord {
  bottom: 150px;
  left: 54px;
}
.hero .telegram-2 {
  bottom: 45px;
  left: 192px;
}
.hero .reddit {
  top: 70px;
  left: 144px;
}
.hero .twitter {
  bottom: 135px;
  right: 103px;
}
.hero .widget {
  width: 496px;
  min-height: 601.58px;
  border-radius: 30px;
  background: rgb(255, 255, 255);
}
.hero__mobile {
  display: none;
}
@media screen and (max-width: 1600px) {
  .hero__wrap {
    gap: 40px;
    max-width: 1200px;
  }
}
@media screen and (max-width: 1200px) {
  .hero__wrap {
    gap: 20px;
    max-width: 1000px;
  }
}
@media screen and (max-width: 1024px) {
  .hero {
    padding: 20px 0 60px;
    position: relative;
    margin-right: 11px;
  }
  .hero__wrap {
    flex-direction: column;
    gap: 0;
  }
  .hero #shmu-widget {
    margin: 0 auto 25px;
    width: 100%;
  }
  .hero .hero-move,
  .hero .hero__shiba {
    display: none;
  }
  .hero__content {
    text-align: center;
    max-width: none;
  }
  .hero__list {
    margin-bottom: 30px;
  }
  .hero__mobile {
    display: block;
  }
  .hero__title {
    font-size: 36px;
    margin-bottom: 13px;
  }
  .hero__subtitle {
    font-size: 16px;
    padding: 0 20px;
    margin-bottom: 15px;
    max-width: 476px;
    width: 100%;
  }
  .hero__btn {
    font-size: 18px;
    max-width: 484px;
  }
}
@media screen and (max-width: 767px) {
  .hero {
    margin: 0 auto;
    padding: 0 10px;
  }
  .hero__title {
    display: none;
  }
  .hero__mobile {
    display: block;
  }
  .hero__item:not(:last-child) {
    margin-right: 30px;
  }
  .hero__item svg {
    width: 100%;
  }
}

