.exchange-item {
  border-radius: 30px;
  background: #dffffb;
  box-shadow: 0 10px 40px 5px rgba(0, 0, 0, 0.05);
  padding: 30px 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.coming-soon-animate {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  text-transform: uppercase;
  color: #1e1e1e;
  font-family: "GothamPro-Bold", sans-serif;
  font-weight: 800;
  font-size: 18px;
  height: 111px;
}
.coming-soon-animate .loader-animate {
  font-size: 11px;
  width: 1em;
  height: 1em;
  margin: 30px auto 0;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  animation: mulShdSpin 1.1s infinite ease;
  transform: translateZ(0);
}
@keyframes mulShdSpin {
  0%,
  100% {
    box-shadow: 0em -2.6em 0em 0em #ffffff, 1.8em -1.8em 0 0em rgb(255, 113, 33),
      2.5em 0em 0 0em rgb(255, 113, 33), 1.75em 1.75em 0 0em rgb(255, 113, 33),
      0em 2.5em 0 0em rgb(255, 113, 33), -1.8em 1.8em 0 0em rgb(255, 113, 33),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.5),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7);
  }
  12.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.7),
      1.8em -1.8em 0 0em #ffffff, 2.5em 0em 0 0em rgb(255, 113, 33),
      1.75em 1.75em 0 0em rgb(255, 113, 33), 0em 2.5em 0 0em rgb(255, 113, 33),
      -1.8em 1.8em 0 0em rgb(255, 113, 33), -2.6em 0em 0 0em rgb(255, 113, 33),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5);
  }
  25% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.5),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7), 2.5em 0em 0 0em #ffffff,
      1.75em 1.75em 0 0em rgb(255, 113, 33), 0em 2.5em 0 0em rgb(255, 113, 33),
      -1.8em 1.8em 0 0em rgb(255, 113, 33), -2.6em 0em 0 0em rgb(255, 113, 33),
      -1.8em -1.8em 0 0em rgb(255, 113, 33);
  }
  37.5% {
    box-shadow: 0em -2.6em 0em 0em rgb(255, 113, 33),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.7), 1.75em 1.75em 0 0em #ffffff,
      0em 2.5em 0 0em rgb(255, 113, 33), -1.8em 1.8em 0 0em rgb(255, 113, 33),
      -2.6em 0em 0 0em rgb(255, 113, 33), -1.8em -1.8em 0 0em rgb(255, 113, 33);
  }
  50% {
    box-shadow: 0em -2.6em 0em 0em rgb(255, 113, 33),
      1.8em -1.8em 0 0em rgb(255, 113, 33),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.5),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.7), 0em 2.5em 0 0em #ffffff,
      -1.8em 1.8em 0 0em rgb(255, 113, 33), -2.6em 0em 0 0em rgb(255, 113, 33),
      -1.8em -1.8em 0 0em rgb(255, 113, 33);
  }
  62.5% {
    box-shadow: 0em -2.6em 0em 0em rgb(255, 113, 33),
      1.8em -1.8em 0 0em rgb(255, 113, 33), 2.5em 0em 0 0em rgb(255, 113, 33),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.5),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.7), -1.8em 1.8em 0 0em #ffffff,
      -2.6em 0em 0 0em rgb(255, 113, 33), -1.8em -1.8em 0 0em rgb(255, 113, 33);
  }
  75% {
    box-shadow: 0em -2.6em 0em 0em rgb(255, 113, 33),
      1.8em -1.8em 0 0em rgb(255, 113, 33), 2.5em 0em 0 0em rgb(255, 113, 33),
      1.75em 1.75em 0 0em rgb(255, 113, 33),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.5),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.7), -2.6em 0em 0 0em #ffffff,
      -1.8em -1.8em 0 0em rgb(255, 113, 33);
  }
  87.5% {
    box-shadow: 0em -2.6em 0em 0em rgb(255, 113, 33),
      1.8em -1.8em 0 0em rgb(255, 113, 33), 2.5em 0em 0 0em rgb(255, 113, 33),
      1.75em 1.75em 0 0em rgb(255, 113, 33), 0em 2.5em 0 0em rgb(255, 113, 33),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.5),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.7), -1.8em -1.8em 0 0em #fff;
  }
}
