.roadmap__title {
  text-align: center;
  margin-bottom: 20px;
}
.roadmap__image {
  max-width: 343px;
  margin: 0 auto 70px;
}
.roadmap .title-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.roadmap .title-icon .icon {
  max-width: 207px;
  margin: 14px auto 0;
}
.roadmap__items {
  position: relative;
  display: grid;
  row-gap: 30px;
}
.roadmap__items:before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  height: 100%;
  border: 2px dashed #32c0cf;
}
.roadmap__item {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 120px;
}
.roadmap__item:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  height: 58px;
  width: 58px;
  border-radius: 50%;
  background-color: #32c0cf;
  border: 5px solid #fff;
  z-index: 5;
}
.roadmap__item > div {
  position: relative;
  z-index: 2;
}
.roadmap__item .title {
  font-family: "GothamPro-Bold", sans-serif;
  font-size: 48px;
  line-height: 1.3;
  text-align: center;
  color: #1e1e1e;
}
.roadmap__item .content {
  background-color: #fff;
  box-shadow: 0 10px 40px 5px rgba(0, 0, 0, 0.05);
  border-radius: 30px;
  padding: 30px 30px 54px 45px;
  width: calc(100% - 20px);
  z-index: 4;
}
.roadmap__item .title-icon:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -50%;
  transform: translateY(-50%);
  display: block;
  width: 74%;
  border: 2px dashed #32c0cf;
  z-index: -1;
}
.roadmap__item p {
  margin-bottom: 30px;
  font-family: "GothamPro-Bold", sans-serif;
  font-size: 24px;
  line-height: 1.3;
  color: #1e1e1e;
}
.roadmap__item ul {
  display: grid;
  row-gap: 30px;
  margin: 0;
  padding: 0;
}
.roadmap__item ul li {
  margin: 0;
  padding: 0 0 0 47px;
  list-style: none;
  font-family: "GothamPro-Regular", sans-serif;
  line-height: 1.3;
  color: #1e1e1e;
}
.roadmap__item ul li:not(:last-child) {
  margin-bottom: 0;
}
.roadmap__item li {
  position: relative;
}
.roadmap__item li:before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  display: block;
}
.roadmap__item ul li:before {
  width: 31px;
  height: 31px;
  background-color: transparent;
  border-radius: 0;
  background-size: contain;
  background-repeat: no-repeat;
