.roadmap__item:nth-child(2n) .title-icon {
  grid-row: 1;
  grid-column: 1;
}
.roadmap__item:nth-child(2n) .title-icon:before {
  right: -50%;
  left: unset;
}
.roadmap__item:nth-child(2n) .content {
  margin: 0 0 0 auto;
}
.roadmap__item:last-child .content:before {
  content: "";
  position: absolute;
  bottom: 0;
  right: -84px;
  display: block;
  width: 6px;
  height: 50%;
  background-color: #affff5;
  z-index: 2;

  @media screen and (max-width: 800px) {
    display: none;
  }
}

