.dashboard__title-mob {
  display: none;
}
.dashboard__wrapper {
  padding: 85px 0 75px;
  margin-right: 40px;
  background: #0c2349;
  box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.35);
  display: flex;
}
.dashboard__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
}
.dashboard__content {
  width: 56%;
  max-width: 581px;
}
.dashboard__content * {
  color: #fff;
}
.dashboard__title {
  margin-bottom: 30px;
  color: white !important;
}
.dashboard__text {
  margin-bottom: 30px;
  max-width: 578px;
  position: relative;
  z-index: 2;
  color: white;
  font-family: GothamPro-Regular;
}
.dashboard__img {
  position: absolute;
  top: -24px;
  right: -1%;
  width: 43%;
  height: 100%;
  max-width: 908px;
}
.dashboard__btn {
  max-width: 180px;
}
@media screen and (max-width: 1700px) {
  .dashboard__img {
    right: 0;
  }
}
.contract-input__wrap input,
.contract-input__wrap select {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 60px;
  background-color: #fff;
  border: 2px solid #a9dbff;
  border-radius: 20px;
  padding: 15px 25px 15px 39px;
  font-weight: 400;
  font-size: 16px;
  color: #1e1e1e;
  word-break: break-all;
}
.contract {
  box-shadow: 0 10px 40px 5px rgba(0, 0, 0, 0.05);
  border-radius: 30px;
  overflow: hidden;
  text-align: left;
  max-width: 930px;
  margin: 0 auto;
}
.contract__wrap {
  background-color: #c8fff9;
  padding: 20px 100px 40px;
}
.contract .title {
  font-family: "GothamPro-Bold", sans-serif;
  font-size: 30px;
  padding: 28px 100px;
  background-color: #fff;
}
.contract__info {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.7;
  max-width: 500px;
  text-align: center;
  margin: 0 auto 30px;
}
.contract__address {
  margin-bottom: 16px;
}
.contract__block {
  position: relative;
  width: 100%;
  border-radius: 20px;
  background-color: #fff;
  overflow: hidden;
}
.contract__block:before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  width: 30%;
  height: calc(100% - 20px);
  background-color: #fff;
  filter: blur(5px);
  -webkit-filter: blur(5px);
}
.contract input:disabled,
.contract input:disabled::placeholder {
  -webkit-text-fill-color: #1e1e1e;
  opacity: 1;
}
.contract__label {
  display: block;
  font-family: "GothamPro-Bold", sans-serif;
  font-size: 18px;
  margin-bottom: 10px;
}
.contract__copy,
.contract__copied,
.contract__currency {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.contract__copy,
.contract__currency {
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contract__copy {
  cursor: pointer;
}
.contract__copy:hover path {
  fill: #f4b794;
}
.contract__currency {
  width: 106px;
  height: 100%;
  right: 0;
  background-color: #c7f3ff;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  line-height: 170%;
  text-align: center;
  color: #1e1e1e;
  border: 2px solid #a9dbff;
  border-left: 0;
  border-radius: 0 20px 20px 0;
}
.contract__copied {
  right: 40px;
  display: none;
  color: #ff7121;
}
.contract__copy path {
  fill: #ff7121;
}
.contract__btns {
  margin: 34px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contract__btn:not(:last-child) {
  margin-right: 38px;
}
.contract__btn {
  max-width: 180px;
}
.presale-calc {
  margin-bottom: 50px;
}
.presale-calc .contract__input,
.presale-calc select {
  text-align: right;
  padding-right: 124px;
  padding-left: 20px;
  font-size: 20px;
}
.presale-calc select {
  margin: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-align: unset;
  padding-left: 40px;
