.sb-info__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  z-index: 4;
}
.sb-info__title,
.sb-info .sb-currency__info {
  font-family: "Noto Sans", sans-serif;
  font-weight: 800;
  font-size: 28px;
  margin-bottom: 26px;
}
.sb-info .sb-currency__info p {
  display: inline-block;
}
.sb-info__btn {
  max-width: 174px;
}
.sb-info__title {
  font-family: "Cocogoose", sans-serif;
  font-weight: 400;
  margin-bottom: 15px;
}
.sb-countdown {
  font-family: "GothamPro-Bold", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sb-countdown__title {
  font-size: 30px;
  color: #fff;
  text-align: center;
  padding-bottom: 15px;
}
.sb-cat {
  position: absolute;
  right: 45px;
  bottom: 0;
  max-height: 534px;
  z-index: 2;
}
.countdown {
  display: inline-block;
  text-align: center;
}
.countdown-number {
  display: inline-block;
}
.countdown-number .countdown-time {
  font-size: 44px;
  color: #0c2349;
  border-radius: 16.71px;
  border: 5.37px solid #3447a6;
  line-height: 1;
  background: #affff5;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  position: relative;
}
.countdown-number:not(:last-child) {
  margin-right: 19px;
}
.countdown-number:not(:last-child) .countdown-time::after {
  content: ":";
  position: absolute;
  top: 2px;
  right: -25px;
  font-size: 53px;
  font-weight: 700;
  font-family: "Noto Sans", sans-serif;
  line-height: 1.5;
  color: #fff;
}
.countdown-text {
  font-size: 16px;
  color: #fff;
  display: block;
  padding-top: 3px;
}
@media screen and (max-width: 1200px) {
  .sb-cat {
    right: 0;
  }
}
@media screen and (max-width: 1024px) {
  .sb-cat {
    display: none;
  }
}
@media screen and (max-width: 767px) {
  .sb-countdown {
    align-items: center;
  }
  .sb-info::before {
    height: 91px;
    background-size: 265%;
  }
  .countdown-number .countdown-time {
    font-size: 35px;
    width: 65px;
    height: 65px;
    border-width: 3px;
  }
  .countdown-number:not(:last-child) .countdown-time::after {
    font-size: 39px;
    top: -2px;
    right: -20px;
  }
  .countdown-number .countdown-text {
    font-size: 13px;
  }
}
.shiba-buy {
  padding: 120px 0 140px;
  margin-bottom: 50px;
  position: relative;
}
.shiba-buy__wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.shiba-buy__title {
  font-size: 48px;
  margin-bottom: 33px;
}
.shiba-buy__content {
  width: 55%;
  display: flex;
  flex-direction: column;
  align-items: end;
  text-align: right;
}
.shiba-buy__content * {
  color: #fff;
}
.shiba-buy__text {
  font-family: "GothamPro-Regular", sans-serif;
  line-height: 1.3;
  max-width: 540px;
  margin-bottom: 40px;
}
.shiba-buy__img {
  max-width: 520px;
  width: 36%;
  position: absolute;
  left: 20%;
  top: -8%;
  z-index: 1;
}
.shiba-buy__btns {
  display: flex;
  width: 100%;
